// content4 코스닥
.content4{
  width: 100%;height: 1200px;
  // min-width: 1550px;
  position: relative;
  //background: #fff;
  display: flex;
  flex-direction: row-reverse;
  // opacity: 0;
  transition: all 1s linear 0s;
  &.on{
    opacity: 1;
  }

  .box_content4{
    //background: red;
    width: 45%;
    .box_in{
      position: relative;top: 50%;
      transform: translateY(-50%);
      //font-family: $h1-font;
      margin: 0 auto;
      width: 60%;
      height: 750px;
      border: 3px solid #049DD9;
      border-radius: 10px;
      color: #333333;
      h2{
        font-family: $subtitle-font;
        color: #049DD9;
        text-indent: 5px;
        font-size: 40px;
        padding: 15px;
        font-weight: 500;
        text-shadow: 1px 1px 2px #b9b9b9;
      }

      .content4_rotate{
        &:nth-of-type(1){
         display: inline-block;
         transform: rotate(270deg);
         position: absolute;left: -75px;top: 340px;
        }
        &:nth-of-type(2){
         display: inline-block;
         transform: rotate(90deg);
         position: absolute;right: -100px;top: 150px;
        }
        &:nth-of-type(3){
         position: absolute;
         transform: rotate(270deg);
         bottom: 190px;left: -20px;
         font-size: 40px;
        }
         }

         .content4_box_in{
          // 두개
          width: 100%;
          position: absolute;bottom: 0;left: 0;
          display: flex;
          ul{
            font-size: 16px;
            margin: 0 auto;
            &:nth-of-type(1){
            padding: 5px 15px;
             width:50%;
             li{
              display: flex;
              justify-content: space-between;
              //color: #fff;
             }
            }
            &:nth-of-type(2){
              padding: 0px 15px;
              width: 50%;
            }
            li{
             // color: #fff;
            }
          }
        }
      .kos_main_mokup{
        width: 100%;height: 100%;
        position: absolute;left: 9%;top: 10%;
        
      .kos_mokup_main_mo{
        //전체적인 크기
         position: absolute;
         top: 0%;left:50%;
         transform: translateX(-50%);    
         width: 330px;height: 550px;
         transition: all 0.2s linear 0s;
         animation: mainka 2s linear infinite; 
       .kos_fram_main_mo{
         //틀
         margin-left: 7px;margin-top:0px;
         width: 73%;height: 95%;
         border-radius: 5px 5px 32px 32px;
        //background: #ffffff;
         overflow: hidden;
      
         .kos_backmokup_main_mo{
          //틀 안에 들어갈것
          position: relative;top: -45px;left: 4px;
          display: block;
          width: 350px;height: 605px;
           video{
            object-fit: contain;height: 643px;width: 245px;
             }
           }
       }
  
       .kos_mokup_img_main_mo{
         //목업
         position: absolute;left: 0;top: 0;clear: both;
         display: block;
         width: 260px;height: 530px;
         background:url(../img/komo.png)no-repeat 0 0;
         background-size: contain;
       }
      }
      }

  
    }
  }


  .kos_box_groub{
    //background: blue;
    position: relative;top: 0;left: 0;
    width: 55%;
    height: 100%;
    // 1
    .content4_box1{
      position: absolute;top: 17%;left: 15%;
      //background: greenyellow;
      //z-index: 10;
      //position: absolute;right: 5%;top: 25%;
      .letter_name{
        color: #049DD9;
        font-size: 65px;
        font-family: $content-main-font;
        text-align: left;
        //font-weight: bold;
        //text-shadow: 2px 2px 2px #656565;
        span{
          text-shadow: none;
          font-size: 30px;
          color: #9ce3ff;
          display: block;
          text-align: left;
          line-height: 35px;
        }
      }
    }

    // 2
    .content4_box2{
      position: absolute;top: 36%;left: 15%;
      //background: pink;
      .workP{
        width: 66%;
        font-size: 22px;
        font-weight: bold;
        color: #1b1b1b;
        font-family: $subtitle-font;
        strong{
          color: #049DD9;
          font-weight: bold;
          //text-shadow: 1px 1px 1px #1b1b1b;
          padding: 0 3px;
        }
      }
      
      .workul1{
        width: 80%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: flex-start;
        padding: 25px 0;
        
        li{
          font-weight: bold;
          //font-size: 16px;
          // color: #E76623;
          // font-weight: bold;
          // line-height: 27px;
          strong{
            padding-left: 15px;
          }
           &:nth-of-type(2){
            padding-left: 80px;
           }
           &:nth-of-type(3){
            display: flex;
            align-items: center;
            
            strong{
              font-size: 20px;
              display: flex;
              letter-spacing: 1px;
              padding: 5px 23px;
              span{
                font-size: 25px;
                padding:0 6px;
              }
            }
           }
         
        }
        
      }
