        width: 80%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: flex-start;
        padding: 25px 0;
        
        li{
          font-weight: bold;
          //font-size: 16px;
          // color: #E76623;
          // font-weight: bold;
          // line-height: 27px;
           &:nth-of-type(2){
            padding-left: 80px;
           }
           &:nth-of-type(3){
            display: flex;
            align-items: center;
            
            strong{
              font-size: 20px;
              display: flex;
              letter-spacing: 1px;
              padding: 0 20px;
              span{
                font-size: 25px;
                padding:0 5px;
              }
            }
           }
         
        }
        
      } */

    }

    .borderline{
      position: absolute;top: 52%;
      display: block;
      width: 60%;height: 3px;
      background: #049dd9d0;
    }

    // 3
    .content4_box3{
      position: absolute;right: 0%;top: 20%;
      //width: 100%;height: 100%;
      //background: blue;
      // margin-top: 50px;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      button{
        cursor: pointer;
        color: #181818;
        background: #e5e5e5;
        border: none;
        margin:18px 0;
        padding: 17px 32px;
        font-size: 19px;
        border-radius: 50px 50px 2px 50px;
        font-weight: bold;
        transition: all 0.2s linear 0s;
        &:hover{
          background: #049DD9;
          color: #ffffff;
          box-shadow: 4px 4px 2px #005d81;
        }
      }
    }

    // 4
    .content4_box4{
      //background: yellow;
      position: absolute;top: 55%;left: 15%;
      width: 85%;height: 30%;
      div{
        width: 100%;height: calc(100% / 3);
        display: flex;
        flex-direction: row;
        align-items: center;
      // border: 1px solid black;
        span{
          display: block;
          text-align: center;
          width: 70px;
          height: 70px;
          border-radius: 50%;
          background: #beedffa4;
          margin-right: 30px;
          svg{
            display: inline-block;
            font-size: 40px;
            transform: translateY(15px);
          }
        }
          ul{
            li{
              font-size: 18px;
              line-height: 30px;
              strong{
               color: #049DD9;
               font-size: 19px;
              }
            }
          }
        
      }
    }

          
  }    
  }