// content5 cjone
.content5{
  width: 100%;height: 1200px;
  // min-width: 1550px;
  position: relative;
  //background: #ffffff;
  display: flex;
  //flex-direction: row-reverse;
  //opacity: 0;
  transition: all 1s linear 0s;
  &.on{
    opacity: 1;
  }

  .box_content5{
    //background: red;
    width: 45%;
    .box_in{
      position: relative;top: 50%;
      transform: translateY(-50%);
      //font-family: $h1-font;
      margin: 0 auto;
      width: 60%;
      height: 750px;
      border: 3px solid #f22259c9;
      border-radius: 10px;
      color: #333333;
      h2{
        color: #F22259;
        //font-family: $content-main-font;
        text-indent: 5px;
        font-size: 40px;
        padding: 15px;
        font-weight: 500;
        text-shadow: 1px 1px 2px #b9b9b9;
      }

      .content5_rotate{
        &:nth-of-type(1){
         display: inline-block;
         transform: rotate(270deg);
         position: absolute;left: -75px;top: 340px;
        }
        &:nth-of-type(2){
         display: inline-block;
         transform: rotate(90deg);
         position: absolute;right: -70px;top: 120px;
        }
        &:nth-of-type(3){
         position: absolute;
         transform: rotate(270deg);
         bottom: 190px;left: -20px;
         font-size: 40px;
        }
         }

         .content5_box_in{
          // 두개
          width: 100%;
          position: absolute;bottom: 0;left: 0;
          display: flex;
          ul{
            font-size: 16px;
            margin: 0 auto;
            &:nth-of-type(1){
            padding: 5px 15px;
             width:50%;
             li{
              display: flex;
              justify-content: space-between;
              //color: #fff;
             }
            }
            &:nth-of-type(2){
              padding: 0px 15px;
              width: 50%;
            }
            li{
             // color: #fff;
            }
          }
        }
        .cj_main_mokup{
          width: 100%;height: 100%;
          position: absolute;top: 10%;left: 10%;
          
        .cj_mokup_main_mo{
          //전체적인 크기
           position: absolute;
           top: 0px;left:50%;
           transform: translateX(-50%);    
           width: 330px;height: 550px;
           transition: all 0.2s linear 0s;
           animation: mainka 2s linear infinite;
    
         .cj_fram_main_mo{
           //틀
           margin-left: 4px;margin-top:22px;
           width: 75%;height: 90%;
           border-radius: 5px 5px 32px 32px;
          background: #101010;
           overflow: hidden;
        
           .cj_backmokup_main_mo{
            //틀 안에 들어갈것
            position: relative;top: -49px;left: 5px;
            display: block;
            width: 250px;height: 605px;
             video{
              object-fit: contain;width: 100%;height: 100%;
               }
             }
         }
  
         .cj_mokup_img_main_mo{
           //목업
           position: absolute;left: 0;top: 0;clear: both;
           display: block;
           width: 260px;height: 530px;
           background:url(../img/netmain.png)no-repeat 0 0;
           background-size: contain;
         }
        }
        }
  }
}


  .cj_box_groub{
    //background: blue;
    position: relative;top: 0;left: 0;
    width: 55%;
    height: 100%;
    // 1
    .content5_box1{
      position: absolute;top: 17%;left:0%;
      //background: greenyellow;
      //z-index: 10;
      //position: absolute;right: 5%;top: 25%;
      .letter_name{
        color: #F22259;
        text-align: center;
        font-size: 75px;
        font-family: $content-main-font;
        //font-weight: bold;
        //text-shadow: 2px 2px 2px #656565;
        span{
          text-shadow: none;
          padding-left: 6px;
          font-size: 30px;
          color: #ff97b3;
          display: block;
          text-align: left;
          line-height: 17px;
        }
      }
    }

    // 2
    .content5_box2{
      position: absolute;top: 30%;left:0%;
      //background: pink;
      .workP{
        width: 63%;
        text-align: right;
        font-size: 22px;
        font-weight: bold;
        color: #1b1b1b;
        font-family: $subtitle-font;
        strong{
          color: #F22259;
          font-weight: bold;
          //text-shadow: 1px 1px 1px #1b1b1b;
          padding: 0 3px;
        }
      }
      .workul1{
        width: 80%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: flex-start;
        padding: 25px 3px;
        
        li{
          font-weight: bold;
          //font-size: 16px;
          // color: #E76623;
          // font-weight: bold;
          // line-height: 27px;
          strong{
            padding-left: 15px;
          }
           &:nth-of-type(2){
            padding-left: 80px;
           }
           &:nth-of-type(3){
            display: flex;
            align-items: center;
            
            strong{
              font-size: 20px;
              display: flex;
              letter-spacing: 1px;
              padding: 5px 20px;
              span{
                font-size: 25px;
                padding:0 6px;
              }
            }
           }
         
        }
        
      }

    }

    .borderline{
      position: absolute;top: 47%;right: 0;
      display: block;
      width: 60%;height: 3px;
      background: #f22259c6;
    }

    // 3
    .content5_box3{
      position: absolute;right: 10%;top: 20%;
      //width: 100%;height: 100%;
      //background: blue;
      // margin-top: 50px;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      button{
        cursor: pointer;
        // background: #ff90ad;
        color: #181818;
        background: #e5e5e5;
        border: none;
        margin:18px 0;
        padding: 17px 32px;
        font-size: 19px;
        border-radius: 50px 50px 2px 50px;
        font-weight: bold;
        transition: all 0.2s linear 0s;
        &:hover{
          background: #F22259;
          color: #ffffff;
          box-shadow: 4px 4px 2px #8b0025;
        }
      }
    }

    // 4
      .content5_box4{
      //background: yellow;
      position: absolute;top: 50%;left: 0%;
      width: 85%;height: 30%;
      div{
        width: 100%;height: calc(100% / 3);
        display: flex;
        flex-direction: row;
        align-items: center;
      // border: 1px solid black;
        span{
          display: block;
          text-align: center;
          width: 70px;
          height: 70px;
          border-radius: 50%;
          background: #ffdbe5;
          margin-right: 30px;
          svg{
            display: inline-block;
            font-size: 45px;
            transform: translateY(12px);
          }
        }
          ul{
            li{
              font-size: 18px;
              line-height: 30px;
              strong{
               color: #F22259;
               font-size: 19px;
              }
            }
          }
        
      }
    }

          
  }    
  }