.container{
  font-family: $content-font;
  //background:linear-gradient(180deg, rgba(254,254,254,1) 43%, rgba(220,219,218,1) 100%);
background-size: cover;
background-attachment: fixed;
width: 100%;
height: 20000px;
overflow-x: hidden;



  // section1
// 소개
.content1{
  position: relative;top: 0;left: 0;;
  width: 100%;height: 1300px;
  //background: #ffebeb;
  font-family: $content-font;

.content1_groub{
  display: flex;
  position: relative;top: 50%;left: 50%;
  width: 80%;height: 85vh;
  transform: translate(-50%, -50%);
  //background: #ffffffcc;
  .inform_box1{
    position: relative;top: 0;left: 0;
    width: 50%;
    //border: 1px solid red;
    .inform1{
     position: absolute;top: 8%;left: 50%;
     transform: translateX(-50%);
     width: 350px;height: 350px;
     border-radius: 700% 500% 400% 500%;
     background: #FFF340;
    }
   /*  .inform2{
      position: absolute;top: 55%;right: 20%;
      //transform: translateX(-50%);
      //text-align: center;
      font-size: 18px;
    } */
    .inform3{
      position: absolute;top:60%;left:50%;
      transform: translateX(-50%);
      ul{
        li{
          font-size: 18px;
          line-height: 37px;
          span{
            display: inline-block;
            padding-right: 15px;
            transform: translateY(3px);
            }
        }
      }
    }
   }

   .inform_box2{
    position: relative;top: 0;right: 0;
    width: 50%;
    //background: rgb(97, 97, 31);
    .inform4{
      position: absolute;top: 5%;left: 5%;
      width: 80%;
      .p_groub1{
        p{
          &:nth-of-type(1){
          font-family: $content-main-font;
          -webkit-text-stroke: 2px #000;
          color: transparent;
          font-size: 50px;
          }
          &:nth-of-type(2){
          font-family: $content-main-font;
          font-size: 50px;
          
          }
      }
      }
      .p_groub2{
        p{
          font-size: 17px;
          color: #525252;
        }
      }
      
     }

    .inform5{
      position: absolute;top: 45%;left: 5%;
      width: 80%;
      .intd{
        font-size: 28px;
        font-family: $content-main-font;
        padding-bottom: 10px;
        &::after{
          z-index: -1;
          position: absolute;top:8px;left: -5px;
          content: "";display: inline-block;
          width: 184px;height: 12px;
          background: #FFF340;
        }
      }
      ul{
        li{
          line-height: 25px;
        }
      }
    }

    .inform6{
      position: absolute;top: 65%;left: 5%;
      width: 80%;
      .intd{
        font-size: 28px;
        font-family: $content-main-font;
        padding-bottom: 10px;
        &::after{
          z-index: -1;
          position: absolute;top:8px;left: -5px;
          content: "";display: inline-block;
          width: 80px;height: 12px;
          background: #FFF340;
        }
      }
     ul{
      display: flex;
      flex-wrap: wrap;
      width: 350px;
      li{
         padding: 0 8px;
         svg{
          width: 28px;
         }
      }
     }
    }
   }
}

.inform_box3{
  .one1{
    position: absolute;bottom: 15%;right: -10%;
    width: 600px;height: 600px;
    border-radius: 50%;
    filter: blur(100px);
    background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(255, 243, 80, 0.415) 100%);
  }
  .one2{
    position: absolute;top: 0%;left: 0;
    width: 600px;height: 600px;
    border-radius: 50%;
    filter: blur(100px);
    background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(255, 243, 80, 0.415) 100%);
  }
}



}
  
