// content2 카카오톡 
// 1550까지만 줄어들게
.content2{
  width: 100%;height: 1200px;
  // min-width: 1550px;
  position: relative;
  //background: #ffffff;
  display: flex;
  flex-direction: row-reverse;
  //opacity: 0;
  transition: all 1s linear 0s;
  &.on{
    opacity: 1;
  }

  .box_content2{
    //background: red;
    width: 45%;
    .box_in{
      position: relative;top: 50%;
      transform: translateY(-50%);
      //font-family: $h1-font;
      margin: 0 auto;
      width: 60%;
      height: 750px;
      border: 3px solid #ffe196;
      border-radius: 10px;
      color: #333333;
      h2{
        //font-family: $subtitle-font;
        color: #ffe196;
        text-indent: 5px;
        font-size: 40px;
        padding: 15px;
        font-weight: 500;
        text-shadow: 1px 1px 2px #b9b9b9;
      }

      .content2_rotate{
        &:nth-of-type(1){
         display: inline-block;
         transform: rotate(270deg);
         position: absolute;left: -75px;top: 340px;
        }
        &:nth-of-type(2){
         display: inline-block;
         transform: rotate(90deg);
         position: absolute;right: -80px;top: 120px;
        }
        &:nth-of-type(3){
         position: absolute;
         transform: rotate(270deg);
         bottom: 190px;left: -20px;
         font-size: 40px;
        }
         }

         .content2_box_in{
          // 두개
          width: 100%;
          position: absolute;bottom: 0;left: 0;
          display: flex;
          ul{
            font-size: 16px;
            margin: 0 auto;
            &:nth-of-type(1){
            padding: 5px 15px;
             width:50%;
             li{
              display: flex;
              justify-content: space-between;
              //color: #fff;
             }
            }
            &:nth-of-type(2){
              padding: 0px 15px;
              width: 50%;
            }
            li{
             // color: #fff;
            }
          }
        }

        .kakao_mokup{
          width: 100%;height: 100%;
          object-fit: contain;
          position:absolute;top:10%;left: 8%;
          //animation: contentkakao 5s linear 0s infinite;
          //transition: all 0.2s linear 0s;
    /*         &:hover{
            transform: rotateY(20deg);
          } */
        .kakao_mokup_main_mo{
          //전체적인 크기
           position: absolute;
           top:0;left:50%;
           transform: translateX(-50%);   
           width: 330px;height: 550px;
           transition: all 0.2s linear 0s;
           //background: blue;
         .kakao_fram_main_mo{
           //틀
           margin-left: 7px;margin-top:22px;
           width:75%;height: 90%;
           border-radius: 5px 5px 32px 32px;
          background: #131313;
           overflow: hidden;
        
           .kakao_backmokup_main_mo{
            //틀 안에 들어갈것
            position: relative;top: -195px;left: 5px;
            display: block;
            width: 240px;height: 900px;
            overflow: hidden;
             video{
              width: 100%;height: 100%;
              object-fit: contain;//height: 893px;width: 247px;
               }
             }
         }
         
         .kakao_mokup_img_main_mo{
           //목업
           position: absolute;left: 0;top: 0;clear: both;
           display: block;
           width: 260px;height: 530px;
           background:url(../img/netmain.png)no-repeat 0 0;
           background-size: contain;
         }
        }
        }

    }
  }


  .kakao_box_groub{
    //background: blue;
    position: relative;top: 0;left: 0;
    width: 55%;
    height: 100%;
    // 1
    .content2_box1{
      position: absolute;top: 17%;left: 15%;
      //background: greenyellow;
      //z-index: 10;
