      .letter_name{
        color: #ffbe1a;
        text-align: center;
        font-size: 75px;
        font-family: $content-main-font;
        //font-weight: bold;
        //text-shadow: 2px 2px 2px #656565;
        span{
          text-shadow: none;
          font-size: 30px;
          color: #e7dac2;
          display: block;
          text-align: left;
          line-height: 15px;
        }
      }
    }

    // 2
    .content2_box2{
      position: absolute;top: 30%;left: 15%;
      //background: pink;
      .workP{
        font-size: 22px;
        font-weight: bold;
        color: #1b1b1b;
        font-family: $subtitle-font;
        strong{
          color: #ffbe1a;
          font-weight: bold;
          //text-shadow: 1px 1px 1px #a9a9a9;
          padding: 0 3px;
        }
      }
      .workul1{
        width: 80%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: flex-start;
        padding: 25px 0;
        
        li{
          font-weight: bold;
          //font-size: 16px;
          // color: #E76623;
          // font-weight: bold;
          // line-height: 27px;
          strong{
            padding-left: 15px;
          }
           &:nth-of-type(2){
            padding-left: 80px;
           }
           &:nth-of-type(3){
            display: flex;
            align-items: center;
            
            strong{
              font-size: 20px;
              display: flex;
              letter-spacing: 1px;
              padding: 5px 23px;
              span{
                font-size: 25px;
                padding:0 6px;
              }
            }
           }
         
        }
        
      }

    }

    .borderline{
      position: absolute;top: 45%;
      display: block;
      width: 60%;height: 3px;
      background: #ffbf57af;
    }

    // 3
    .content2_box3{
      position: absolute;right: 0%;top: 20%;
      //width: 100%;height: 100%;
      //background: blue;
      // margin-top: 50px;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      button{
        cursor: pointer;
        color: #181818;
        background: #e5e5e5;
        border: none;
        margin:18px 0;
        padding: 17px 32px;
        font-size: 19px;
        border-radius: 50px 50px 2px 50px;
        font-weight: bold;
        transition: all 0.2s linear 0s;
        &:hover{
          background: #ffbe1a;
          color: #ffffff;
          box-shadow: 4px 4px 2px #bf8900;
        }
      }
    }

    .content2_box4{
      //background: yellow;
      position: absolute;top: 50%;left: 15%;
      width: 85%;height: 30%;
      div{
        width: 100%;height: calc(100% / 3);
        display: flex;
        flex-direction: row;
        align-items: center;
      // border: 1px solid black;
        span{
          display: block;
          text-align: center;
          width: 70px;
          height: 70px;
          border-radius: 50%;
          background: #fff3d49d;
          margin-right: 30px;
          svg{
            display: inline-block;
            font-size: 45px;
            transform: translateY(12px);
          }
        }
          ul{
            li{
              font-size: 18px;
              line-height: 30px;
              strong{
               color: #ffbe1a;
               font-size: 19px;
              }
            }
          }
        
      }
    }

